import React from 'react'
import { isFinite } from 'lodash';

import IconButton from '@mui/material/IconButton';
import TextField from '@mui/material/TextField';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Typography from '@mui/material/Typography';

import { BiChevronDown } from 'react-icons/bi'

function YearOrMonthMenu(props) {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const handleSelect = (value) => {

        let len = parseInt(props.serviceLength);
        let label = value;

        if (isFinite(len) && len === 1) {
            label = label.slice(0, -1);
        }

        handleClose();

        if (props.setYearOrMonth) {
            props.setYearOrMonth(label);
        }
    }

    return (
        <div>
            <IconButton
                color="default"
                disableRipple={true}
                size="small"
                onClick={handleClick}
                sx={{ pr: 0, backgroundColor: "transparent", "&:hover": { backgroundColor: "transparent" } }}
            >
                <Typography sx={{ mr: 0.5 }}>{props.yearOrMonth ?? "Months"}</Typography><BiChevronDown />
            </IconButton>
            <Menu
                id="basic-menu"
                anchorEl={anchorEl}
                open={open}
                onClose={handleClose}
                MenuListProps={{
                    'aria-labelledby': 'basic-button',
                }}
            >
                <MenuItem onClick={() => handleSelect("Years")}>Years</MenuItem>
                <MenuItem onClick={() => handleSelect("Months")}>Months</MenuItem>
            </Menu>
        </div>
    );
}

export default function YearOrMonth(props) {

    const [serviceLength, setServiceLength] = React.useState(props.serviceLength ?? "");

    const handleChange = (value) => {

        if (isFinite(parseInt(value)) && parseInt(value) > 0) {

            setServiceLength(value);
            if (props.setServiceLength) {
                props.setServiceLength(value);
            }

        } else {
            setServiceLength("");
        }

    }

    return (
        <TextField
            value={serviceLength}
            onChange={(e) => handleChange(e.target.value)}
            size="small"
            label={props.label ?? "Service Length"}
            required={!props.isEdit ?? true}
            variant={props.variant !== null ? props.variant : "outlined"}
            InputProps={{
                endAdornment: YearOrMonthMenu(props),
            }}
            inputProps={{
                style: { textAlign: 'center' },
                readOnly: props.isEdit ?? false
            }}
            sx={{ width: 230 }}
        />
    )
}
