import React from 'react';

// MUI Components
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';
import nationalityList from '../constants/nationality';

// react-icons
import { FaGlobeAsia } from 'react-icons/fa';

const Nationality = (props) => {

    const [nationality, setNationality] = React.useState(props.nationality ?? '');

    const nationalityOptions = nationalityList.map(data => {
        return {
            value: data,
            label: data,
        }
    });

    const handleChange = (value) => {

        setNationality(value);

        if (props.setNationality) {
            props.setNationality(value)
        }

    }

    return (
        <div>
            <TextField
                select
                label="Nationality"
                name="nationality"
                size={props.size ?? "small"}
                variant={props.variant !== null ? props.variant : "outlined"}
                value={nationality}
                onChange={(e) => handleChange(e.target.value)}
                fullWidth
                required
                InputProps={{
                    startAdornment: props.withAdornment ? <FaGlobeAsia size={20} style={{ marginRight: 10,  color: "#00BFA5" }} /> : ""
                }}
                inputProps={{MenuProps: {disableScrollLock: true}}}
            >
                {nationalityOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                        {option.label}
                    </MenuItem>
                ))}
            </TextField>

        </div>
    )
}

export default Nationality;