import React from 'react';
import utilityService from '../../../../services/utils/utilityServices';

// Redux
import { useSelector } from 'react-redux'; 

// MUI Components
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';

// Page Component
import LinearProgressBar from '../../shared/LinearProgressBar/LinearProgressBar';



const EmploymentTypes = (props) => {
 
    const employmentTypeRedux = useSelector((state) => state.masterfiles.value); 
    const [employmentType, setEmploymentType] = React.useState(props.employmentType ?? '');

    const employmentTypeOptions = employmentTypeRedux.masterfiles.employmentTypes.map(data => {
        return {
            value: data.id,
            label: utilityService.capitalizeFirstLetter(data.type)
        }
    });

    const handleChange = (value) => {

        setEmploymentType(value)
        if (props.setEmploymentType) {
            props.setEmploymentType(value)
        }

        if (props.setLabel) {
            let label = employmentTypeRedux.employmentTypes.filter(data => {
                return value === data.id;
            });

            props.setLabel(label[0].type)
        }
    }
 

    return (
        <div>
            {employmentTypeRedux.loading && <LinearProgressBar title={employmentTypeRedux.status} />}
            {!employmentTypeRedux.loading &&
                <TextField
                    select
                    defaultValue=""
                    label="Employment Status"
                    name="employmentType"
                    size="small"
                    variant={props.variant !== null ? props.variant : "outlined"}
                    value={employmentType}
                    onChange={(e) => handleChange(e.target.value)}
                    fullWidth
                    required={!props.isEdit ?? true}
                    disabled={employmentTypeRedux.loading}
                    inputProps={
                        { readOnly: props.isEdit ?? false }
                    }
                >
                    {employmentTypeOptions.map((option) => (
                        <MenuItem key={option.value} value={option.value}>
                            {option.label}
                        </MenuItem>
                    ))}
                </TextField>
            }
        </div>
    )
}

export default EmploymentTypes;