import React from 'react';
// redux
import { useDispatch, useSelector } from 'react-redux';
import { me as getMe } from '../../../redux/async/authAsync';
import { getMember } from '../../../redux/async/membersAsync';
// repository
import businessRepository from '../../../repositories/businessRepository';
// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import LoadingButton from '@mui/lab/LoadingButton';
import Box from '@mui/material/Box';

// Form Components 
import BusinessTypes from './fields/BusinessTypes';
import YearOrMonth from './fields/YearOrMonth';
import toastService from '../../../services/toast/toastService';

export default function NewDetailsBusinessForm(props) {

    const dispatch = useDispatch();
    const authRedux = useSelector((state) => state.auth.value);
    const memberRedux = useSelector((state) => state.members.value);

    const role = authRedux.role.id;
    const me = authRedux?.me;
    let uuid = memberRedux?.selectedMember?.user?.uuid;

    if (role === 5) {
        uuid = me?.uuid;
    }

    const [loading, setLoading] = React.useState(false);

    // Work or Business Financial Details 
    const [businessType, setBusinessType] = React.useState('');
    const [position, setPosition] = React.useState("");
    const [lengthInService, setLengthInService] = React.useState("");
    const [yearOrMonth, setYearOrMonth] = React.useState("Years");
    const [companyName, setCompanyName] = React.useState("");
    const [companyAddress, setCompanyAddress] = React.useState("");

    const handleSubmit = async (e) => {
        e.preventDefault();

        try {
            setLoading(true);

            let data = {
                uuid,
                business_type_id: businessType,
                position,
                company: companyName,
                service_length: `${lengthInService} ${yearOrMonth}`,
                address: companyAddress,
            }

            await businessRepository.post(data);

            if (role === 5) {
                dispatch(getMe());
            } else {
                dispatch(getMember(data.uuid));
            }

            setLoading(false);

            props.toggle();

        } catch (error) {
            setLoading(false);
            toastService.error(error);
        }
    }

    const handleChangeLengthInService = (value, spouse = false) => {

        let len = parseInt(value);
        let label = yearOrMonth;
        let lastChar = label.slice(label.length - 1);

        // Checking if plural or not
        if (isFinite(len) && len === 1) {

            if (lastChar.toLowerCase() === 's') {
                label = label.slice(0, -1);
            }

        } else if (isFinite(len) && len > 1) {

            if (lastChar.toLowerCase() !== 's') {
                label = `${label}s`;
            }

        }

        setLengthInService(value);
        setYearOrMonth(label);

    }


    React.useEffect(() => {
        return () => {
            if (role === 5) {
                dispatch(getMe());
            } else {
                dispatch(getMember(uuid));
            }
        }
    }, [dispatch, uuid, role])

    return (
        <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
            <Box sx={{ m: 2 }}>
                <Grid container spacing={2}>
                    <Grid item xs={6}>
                        <BusinessTypes
                            setBusinessType={setBusinessType}
                            businessType={businessType}
                            variant="outlined"
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <TextField
                            onChange={(e) => setCompanyName(e.target.value)}
                            value={companyName}
                            variant="outlined"
                            size="small"
                            required
                            fullWidth
                            id="companyName"
                            label="Company"
                            name="companyName"
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <TextField
                            onChange={(e) => setCompanyAddress(e.target.value)}
                            value={companyAddress}
                            variant="outlined"
                            size="small"
                            required
                            fullWidth
                            id="companyAddress"
                            label="Company Address"
                            name="companyAddress"
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <TextField
                            onChange={(e) => setPosition(e.target.value)}
                            value={position}
                            variant="outlined"
                            size="small"
                            required
                            fullWidth
                            id="position"
                            label="Position"
                            name="position"
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <YearOrMonth
                            serviceLength={lengthInService}
                            setServiceLength={(value) => handleChangeLengthInService(value, false)}
                            yearOrMonth={yearOrMonth}
                            setYearOrMonth={setYearOrMonth}
                            variant="outlined"
                        />
                    </Grid>
                </Grid>
            </Box>
            <Box display="flex" flexDirection="row" justifyContent="flex-end" sx={{ mt: 2 }}>
                <LoadingButton
                    sx={{ width: 150 }}
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    loading={loading}
                >
                    Create
                </LoadingButton>
                <LoadingButton
                    sx={{ width: 150, ml: 1 }}
                    type="button"
                    fullWidth
                    variant="contained"
                    color="secondary"
                    onClick={() => props.toggle()}
                    loading={loading}
                >
                    Cancel
                </LoadingButton>
            </Box>
        </form>
    )
}
