import * as React from 'react';

// MUI Components
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';

// Page Components
import TabPanel from './components/TabPanel1';
import FamilyMembersTable from '../tables/FamilyMembersTable';
import SpouseForm from '../forms/SpouseForm';

// react-icons
import { FaUserFriends } from 'react-icons/fa';


export default function FamilyMembersTabs(props) {

    const [value, setValue] = React.useState(0);

    const handleChange = (newValue) => {
        setValue(newValue);
    };

    return (
        <Box sx={{ m: 1, bgcolor: 'background.paper' }}>
            <Paper sx={{ p: 2 }}>
                <Tabs
                    value={value}
                    sx={{ p: 1 }}
                >
                    <Tab onClick={() => handleChange(0)} label={
                        <Typography variant="caption1">
                            <Box display="flex">
                                <FaUserFriends size={15} style={{ marginRight: 5 }} />
                                Family
                            </Box>
                        </Typography>
                    } />
                    <Tab onClick={() => handleChange(1)} label={
                        <Typography variant="caption1">
                            <Box display="flex">
                                <FaUserFriends size={15} style={{ marginRight: 5 }} />
                                Spouse
                            </Box>
                        </Typography>
                    } />
                </Tabs>
                <TabPanel value={value} index={0}>
                    <FamilyMembersTable />
                </TabPanel>
                <TabPanel value={value} index={1}>
                    <SpouseForm />
                </TabPanel>
            </Paper>
        </Box>
    );
}
