import React from 'react'
import { parseISO, format, toDate } from 'date-fns';
// Services
import toastService from '../../../services/toast/toastService';
import utilityService from '../../../services/utils/utilityServices';
// Redux
import { useDispatch, useSelector } from 'react-redux';
import { me as getMe } from '../../../redux/async/authAsync';
import { getMember } from '../../../redux/async/membersAsync';
// Repository
import profileRepository from '../../../repositories/profileRepository';
// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
import LoadingButton from '@mui/lab/LoadingButton';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import CircularProgress from '@mui/material/CircularProgress';
// Form Components
import Gender from './fields/Gender';
import Nationality from './fields/Nationality';
import CivilStatus from './fields/CivilStatus';
import Birthdate from './fields/Birthdate';
import ContactNumber from './fields/ContactNumber';
// react-icons
import { FaSave } from 'react-icons/fa';


export default function ProfileForm() {

    const dispatch = useDispatch();
    const authRedux = useSelector((state) => state.auth.value);
    const memberRedux = useSelector((state) => state.members.value);

    const role = authRedux.role.id;

    let me = authRedux.me;
    let profile = memberRedux?.selectedMember;

    if (role === 5) {
        profile = me;
    }


    const [isEdit, setIsEdit] = React.useState(false);
    const [loading, setLoading] = React.useState(false);

    // Basic Profile  
    const [lastName, setLastName] = React.useState(profile?.last_name ?? "");
    const [firstName, setFirstName] = React.useState(profile?.first_name ?? "");
    const [middleName, setMiddleName] = React.useState(profile?.middle_name ?? "");
    const [nameSuffix, setNameSuffix] = React.useState(profile?.suffix ?? "");
    const [birthdate, setBirthdate] = React.useState(parseISO(profile?.birthdate ?? Date.now()) ?? null);
    const [gender, setGender] = React.useState(profile?.gender[0] ?? "");
    const [nationality, setNationality] = React.useState(profile?.nationality ?? "");
    const [civilStatus, setCivilStatus] = React.useState(utilityService.capitalizeFirstLetter(profile?.civil_status) ?? "");
    const [contactNumber, setContactNumber] = React.useState(profile?.contact_no.replace("+63", "") ?? "");
    const [fbLink, setFbLink] = React.useState(profile?.fb_link ?? "");

    const handleSubmit = async (e) => {
        e.preventDefault();

        try {
            setLoading(true);
            setIsEdit(false);
            let bdate = format(toDate(birthdate), "Y-M-d");

            let data = {
                uuid: profile.user?.uuid,
                lastName,
                firstName,
                middleName,
                nameSuffix,
                birthdate: bdate,
                gender,
                nationality,
                civilStatus: civilStatus.toLowerCase(),
                contactNumber,
                fbLink,
            }

            await profileRepository.put(data);

            if (role === 5) {
                dispatch(getMe());
            } else {
                dispatch(getMember(data.uuid));
            }

            setIsEdit(true);
            setLoading(false);
        } catch (error) {
            toastService.error(error);
            setLoading(false);
        }
    }

    React.useEffect(() => {
        let mounted = false;

        if (role === 5) {
            dispatch(getMe());
        } else {
            dispatch(getMember(profile.uuid));
        }

        if (!mounted) {

        }

        return () => mounted = true;
    }, [dispatch, profile.uuid, role]);

    return (
        <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
            <div>
                <Paper sx={{ p: 5, m: 1 }}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sx={{ mb: 1 }}>
                            <FormControlLabel
                                sx={{ mr: 1 }}
                                control={
                                    loading ?
                                        <Typography sx={{ ml: 1 }}>Saving... <CircularProgress size={15} /></Typography>
                                        :
                                        <Switch checked={isEdit} onChange={() => setIsEdit(!isEdit)} size="small" sx={{ ml: 1 }} />}
                                label={loading ? "" : "Edit"}
                                disabled={loading}
                            />
                            {isEdit &&
                                <LoadingButton
                                    sx={{
                                        pt: 0,
                                        pb: 0,
                                        width: 75,
                                        '&:hover': {
                                            backgroundColor: 'transparent'
                                        },
                                        '&:before': {
                                            content: "'|'",
                                            ml: -1,
                                            mr: 1,
                                            color: '#000'
                                        },
                                        fontSize: 14,
                                        color: '#1976D2'
                                    }}
                                    type="submit"
                                    fullWidth
                                    variant="text"
                                    color="primary"
                                    loading={loading}
                                >
                                    <FaSave size={14} style={{ marginRight: 5 }} />
                                    Save
                                </LoadingButton>
                            }
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setLastName(e.target.value)}
                                value={lastName}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="lastName"
                                label="Last Name"
                                placeholder="Last Name"
                                name="lastName"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setFirstName(e.target.value)}
                                value={firstName}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="firstName"
                                label="First Name"
                                placeholder="First Name"
                                name="firstName"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setMiddleName(e.target.value)}
                                value={middleName}
                                variant="standard"
                                size="small"
                                fullWidth
                                id="middleName"
                                label="Middle Name"
                                placeholder="Middle Name"
                                name="middleName"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setNameSuffix(e.target.value)}
                                value={nameSuffix}
                                variant="standard"
                                size="small"
                                fullWidth
                                id="nameSuffix"
                                label="Name Suffix"
                                placeholder="Name Suffix"
                                name="nameSuffix"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={4}>
                            {!isEdit &&
                                <TextField
                                    value={format(birthdate, "MMM dd, yyyy")}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="Birthdate"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <Birthdate setBirthdate={setBirthdate} birthdate={birthdate} variant="standard" />
                            }
                        </Grid>
                        <Grid item xs={4}>
                            {!isEdit &&
                                <TextField
                                    value={gender.toLowerCase() === "m" ? "Male" : "Female"}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="Gender"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <Gender setGender={setGender} gender={gender} variant="standard" isRequired={isEdit} />
                            }
                        </Grid>
                        <Grid item xs={4}>
                            {!isEdit &&
                                <TextField
                                    value={nationality}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="Nationality"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <Nationality setNationality={setNationality} nationality={nationality} variant="standard" />
                            }
                        </Grid>
                        <Grid item xs={4}>
                            {!isEdit &&
                                <TextField
                                    value={civilStatus}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="Civil Status"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <CivilStatus setCivilStatus={setCivilStatus} civilStatus={civilStatus} variant="standard" />
                            }
                        </Grid>
                        <Grid item xs={4}>
                            {!isEdit &&
                                <TextField
                                    value={`+63${contactNumber}`}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="Contact Number"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <TextField
                                    onChange={(e) => setContactNumber(e.target.value)}
                                    value={contactNumber}
                                    variant="standard"
                                    size="small"
                                    required
                                    fullWidth
                                    id="contactNumber"
                                    label="Contact Number"
                                    name="contactNumber"
                                    InputProps={{
                                        inputComponent: ContactNumber
                                    }}
                                />
                            }
                        </Grid>
                        <Grid item xs={4}>
                            {!isEdit &&
                                <TextField
                                    value={fbLink}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="FaceBook Link"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <TextField
                                    onChange={(e) => setFbLink(e.target.value)}
                                    value={fbLink}
                                    variant="standard"
                                    size="small"
                                    required
                                    fullWidth
                                    id="fbLink"
                                    label="FaceBook Link"
                                    name="fbLink"
                                />
                            }
                        </Grid>
                    </Grid>
                </Paper>
            </div>
        </form>
    )
}
