import React from 'react';
import utilityService from '../../../../services/utils/utilityServices';

// Redux
import {  useSelector } from 'react-redux'; 

// MUI Components
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';

// Page Component
import LinearProgressBar from '../../shared/LinearProgressBar/LinearProgressBar';



const OrganizationTypes = (props) => {

    const organizationTypesRedux = useSelector((state) => state.masterfiles.value);
    const [organizationType, setOrganizationType] = React.useState(props.organizationType ?? '');

    const organizationTypeOptions = organizationTypesRedux.masterfiles.organizationTypes.map(data => {
        return {
            value: data.id,
            label: utilityService.capitalizeFirstLetter(data.type)
        }
    });

    const handleChange = (value) => {

        setOrganizationType(value)
        if (props.setOrganizationType) {
            props.setOrganizationType(value)
        }

        if (props.setLabel) {
            let label = organizationTypesRedux.organizationTypes.filter(data => {
                return value === data.id;
            });

            props.setLabel(label[0].type)
        }
    }

    return (
        <div>
            {organizationTypesRedux.loading && <LinearProgressBar title={organizationTypesRedux.status} />}
            {!organizationTypesRedux.loading &&
                <TextField
                    select
                    defaultValue=""
                    label="Organization Type"
                    name="organizationType"
                    size="small"
                    variant={props.variant !== null ? props.variant : "outlined"}
                    value={organizationType}
                    onChange={(e) => handleChange(e.target.value)}
                    fullWidth
                    required={!props.isEdit ?? true}
                    disabled={organizationTypesRedux.loading}
                    inputProps={
                        { readOnly: props.isEdit ?? false }
                    }
                >
                    {organizationTypeOptions.map((option) => (
                        <MenuItem key={option.value} value={option.value}>
                            {option.label}
                        </MenuItem>
                    ))}
                </TextField>
            }
        </div>
    )
}

export default OrganizationTypes;