import React from 'react';
// Services 
import toastService from '../../../services/toast/toastService';
import utilityService from '../../../services/utils/utilityServices';
// Redux
import { useDispatch, useSelector } from 'react-redux';
import { me as getMe } from '../../../redux/async/authAsync';
import { getMember } from '../../../redux/async/membersAsync';
// Repository
import spouseRepository from '../../../repositories/spouseRepository';
// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
import LoadingButton from '@mui/lab/LoadingButton';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import CircularProgress from '@mui/material/CircularProgress';
// Form Components
import YearOrMonth from './fields/YearOrMonth';
import ContactNumber from './fields/ContactNumber';
// react-icons
import { FaSave } from 'react-icons/fa';

export default function SpouseForm(props) {

    const dispatch = useDispatch();
    const authRedux = useSelector((state) => state.auth.value);
    const memberRedux = useSelector((state) => state.members.value);

    const role = authRedux.role.id;
    let uuid = memberRedux?.selectedMember?.user?.uuid;

    let me = authRedux.me;
    let spouse = memberRedux?.selectedMember?.spouse;

    if (role === 5) {
        spouse = me.spouse;
        uuid = me?.user?.uuid;
    }

    let yearOrMonth = spouse?.length_of_service.split(" ") ?? [];

    const [isEdit, setIsEdit] = React.useState(false);
    const [loading, setLoading] = React.useState(false);

    const [spouseName, setSpouseName] = React.useState(spouse?.name ?? "");
    const [spouseOccupation, setSpouseOccupation] = React.useState(spouse?.occupation ?? "");
    const [spouseContactNumber, setSpouseContactNumber] = React.useState(spouse?.contact_no ?? "");
    const [spouseLengthInService, setSpouseLengthInService] = React.useState(yearOrMonth[0] ?? "");
    const [spouseYearOrMonth, setSpouseYearOrMonth] = React.useState(yearOrMonth[1] ?? "");

    const handleChangeLengthInService = (value, spouse = false) => {

        let len = parseInt(value);
        let label = spouseYearOrMonth;
        let lastChar = label.slice(label.length - 1);

        // Checking if plural or not
        if (isFinite(len) && len === 1) {

            if (lastChar.toLowerCase() === 's') {
                label = label.slice(0, -1);
            }

        } else if (isFinite(len) && len > 1) {

            if (lastChar.toLowerCase() !== 's') {
                label = `${label}s`;
            }

        }

        setSpouseLengthInService(value);
        setSpouseYearOrMonth(label);

    }

    const handleSubmit = async (e) => {
        e.preventDefault();

        try {
            setLoading(true);
            setIsEdit(false);

            let data = {
                uuid,
                id: spouse.id,
                name: spouseName,
                occupation: spouseOccupation,
                contact_no: spouseContactNumber,
                length_of_service: `${spouseLengthInService} ${spouseYearOrMonth}`,
            }

            await spouseRepository.put(data);

            if (role === 5) {
                dispatch(getMe());
            } else {
                dispatch(getMember(data.uuid));
            }

            setIsEdit(true);
            setLoading(false);
        } catch (error) {
            toastService.error(error);
            setLoading(false);
        }
    }

    React.useEffect(() => {
        let mounted = false;

        if (role === 5) {
            dispatch(getMe());
        } else {
            dispatch(getMember(uuid));
        }

        if (!mounted) {

        }

        return () => mounted = true;
    }, [dispatch, uuid, role]);

    return (
        <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
            <div>
                <Paper elevation={0} square sx={{ p: 1, m: 1 }}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sx={{ mb: 1 }}>
                            <FormControlLabel
                                sx={{ mr: 1 }}
                                control={
                                    loading ?
                                        <Typography sx={{ ml: 1 }}>Saving... <CircularProgress size={15} /></Typography>
                                        :
                                        <Switch checked={isEdit} onChange={() => setIsEdit(!isEdit)} size="small" sx={{ ml: 1 }} />}
                                label={loading ? "" : "Edit"}
                                disabled={loading}
                            />{isEdit &&
                                <LoadingButton
                                    sx={{
                                        pt: 0,
                                        pb: 0,
                                        width: 75,
                                        '&:hover': {
                                            backgroundColor: 'transparent'
                                        },
                                        '&:before': {
                                            content: "'|'",
                                            ml: -1,
                                            mr: 1,
                                            color: '#000'
                                        },
                                        fontSize: 14,
                                        color: '#1976D2'
                                    }}
                                    type="submit"
                                    fullWidth
                                    variant="text"
                                    color="primary"
                                    loading={loading}
                                >
                                    <FaSave size={14} style={{ marginRight: 5 }} />
                                    Save
                                </LoadingButton>
                            }
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => setSpouseName(e.target.value)}
                                value={spouseName}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="spouseName"
                                label="Spouse Name"
                                name="spouseName"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => setSpouseContactNumber(e.target.value)}
                                value={spouseContactNumber}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="spouseContactNumber"
                                label="Spouse Mobile No."
                                name="spouseContactNumber"
                                InputProps={{
                                    inputComponent: ContactNumber
                                }}
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => setSpouseOccupation(e.target.value)}
                                value={spouseOccupation}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="spouseOccupation"
                                label="Spouse Occupation"
                                name="spouseOccupation"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <YearOrMonth
                                serviceLength={spouseLengthInService}
                                setServiceLength={(value) => handleChangeLengthInService(value, true)}
                                yearOrMonth={utilityService.capitalizeFirstLetter(spouseYearOrMonth)}
                                setYearOrMonth={setSpouseYearOrMonth}
                                isEdit={!isEdit}
                                variant="standard"
                            />
                        </Grid>
                    </Grid>
                </Paper>
            </div>
        </form>
    )
}
