 import ProfileForm from '../../forms/ProfileForm';
import AddressTabs from '../AddressTabs';
import FamilyMembersTabs from '../FamilyMembersTabs';
import EmploymentForm from '../../forms/EmploymentForm';
import BusinessForm from '../../forms/BusinessForm';
import AttachmentTabs from '../AttachmentTabs';

// react-icons
import {
    FaFolder, 
    FaMapMarkerAlt,
    FaSuitcase, 
    FaUserAlt,
    FaUserFriends,
    FaUserTie
} from 'react-icons/fa';


const options = [    
    {
        name: 'Profile',
        icon: <FaUserAlt size={15} style={{ marginRight: 5 }} />,
        content: <ProfileForm />,
    },
    {
        name: 'Address',
        icon: <FaMapMarkerAlt size={15} style={{ marginRight: 5 }} />,
        content: <AddressTabs />,
    },
    {
        name: 'Family',
        icon: <FaUserFriends size={15} style={{ marginRight: 5 }} />,
        content: <FamilyMembersTabs />,
    },
    {
        name: 'Work',
        icon: <FaUserTie size={15} style={{ marginRight: 5 }} />,
        content: <EmploymentForm />,
    }, 
    {
        name: 'Business',
        icon: <FaSuitcase size={15} style={{ marginRight: 5 }} />,
        content: <BusinessForm />
    },
    {
        name: 'Attachments',
        icon: <FaFolder size={15} style={{ marginRight: 5 }} />,
        content: <AttachmentTabs />
    }, 
];

export default options;