import React from 'react'
import { Box } from '@mui/system';

function TabPanel1(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            {...other}
            style={{
                backgroundColor: "#FFF",
                width: '100%',
                maxHeight: '100%',
                overflow: 'hidden',
            }}
        >
            {value === index &&
                <Box sx={{ p: 0, m: 1, mt: 0 }}>
                    {children}
                </Box>
            }
        </div >
    );
}

export default TabPanel1
