import * as React from 'react';
import utilityService from '../../../services/utils/utilityServices';
import alertService from '../../../services/alert/alertService';
import { format, parseISO } from 'date-fns';
// redux
import { useSelector, useDispatch } from 'react-redux';
import { me as getMe } from '../../../redux/async/authAsync';
import { getMember } from '../../../redux/async/membersAsync';
// repository
import familyRepository from '../../../repositories/familyRepository';
// MUI Components
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import FamilyMembersModal from '../modals/FamilyMembersModal';
// react-icons
import { FaEdit, FaTrash, FaPlusCircle } from 'react-icons/fa';


export default function FamilyMembersTable(props) {

    const dispatch = useDispatch();
    const authRedux = useSelector((state) => state.auth.value);
    const memberRedux = useSelector((state) => state.members.value);

    const role = authRedux.role.id;
    const me = authRedux.me;

    let familyMembers = memberRedux?.selectedMember?.family;
    let uuid = memberRedux?.selectedMember?.user?.uuid;

    if (role === 5) {
        familyMembers = me?.family;
        uuid = me?.user.uuid;
    }

    const [action, setAction] = React.useState("New");
    const [rowData, setRowData] = React.useState({});
    const [showAddFamilyMember, setShowAddFamilyMember] = React.useState(false);
    const toggleShowFamilyMember = () => setShowAddFamilyMember(!showAddFamilyMember);

    const handleAdd = () => {
        setAction("New");
        setRowData({});
        toggleShowFamilyMember();
    }

    const handleEdit = (rowData) => {
        setAction("Edit");
        setRowData(rowData);
        toggleShowFamilyMember();
    }

    const handleRemove = (id) => {
        let result = alertService.delete(1, "Remove this record?");

        result.then(opt => {
            if (opt.isConfirmed) {

                familyRepository.destroy({ ids: [id] });

                if (role === 5) {
                    dispatch(getMe());
                } else {
                    dispatch(getMember(uuid));
                }
            }
        })
    }

    return (
        <div>
            {showAddFamilyMember && <FamilyMembersModal toggle={toggleShowFamilyMember} open={showAddFamilyMember} action={action} rowData={rowData} />}
            <TableContainer component={Paper}>
                <Table sx={{ minWidth: 650 }} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell sx={{ minWidth: 100 }}>Name</TableCell>
                            <TableCell sx={{ minWidth: 100 }}>Birthdate</TableCell>
                            <TableCell align='center' sx={{ minWidth: 100 }}>Relation</TableCell>
                            <TableCell align='center' sx={{ minWidth: 100 }}>Beneficiary</TableCell>
                            <TableCell align='center' sx={{ minWidth: 100 }}>Options</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {familyMembers.map((row, index) => {
                            let birthdate = "N/A";
                            if (row.birthdate !== null) {
                                birthdate = format(parseISO(row.birthdate), "MMMM dd, Y");
                            }

                            return (
                                <TableRow
                                    key={`row-${index}`}
                                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                >
                                    <TableCell component="th" scope="row">
                                        {row.name}
                                    </TableCell>
                                    <TableCell >{birthdate}</TableCell>
                                    <TableCell align="center">{utilityService.capitalizeFirstLetter(row.relation)}</TableCell>
                                    <TableCell align="center">{row.isBeneficiary ? "Yes" : "No"}</TableCell>
                                    <TableCell align="center">
                                        <Button color="primary" sx={{ minWidth: 30 }} onClick={() => handleEdit(row)}>
                                            <FaEdit />
                                        </Button>
                                        <Button color="secondary" sx={{ minWidth: 30 }} onClick={() => handleRemove(row.id)}>
                                            <FaTrash />
                                        </Button>
                                    </TableCell>
                                </TableRow>
                            )
                        })}
                        {familyMembers.length <= 0 &&
                            <TableRow >
                                <TableCell colSpan={5} component="th" scope="row" align='center'>
                                    No Records Added
                                </TableCell>
                            </TableRow>
                        }
                    </TableBody>
                </Table>
            </TableContainer>

            <Box display="flex" flexDirection="row" justifyContent="right">
                <Button variant="contained" sx={{ mt: 1 }} onClick={() => handleAdd()}>Add <FaPlusCircle style={{ marginLeft: 5 }} /></Button>
            </Box>
        </div>
    );
}
