import React from 'react'
import config from '../../../config/config';
import Scrollbars from 'react-custom-scrollbars-2';
import alertServices from '../../../services/alert/alertService';

// Redux
import { useSelector } from 'react-redux';

// MUI Components
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
import Tooltip from '@mui/material/Tooltip';
import Checkbox from '@mui/material/Checkbox';

// Page Components
import NoData from '../../components/shared/NoData/NoData';

// react-icons
import { FaCheckCircle, FaSave, FaTimes } from 'react-icons/fa';
import { BiCircle } from 'react-icons/bi';

export default function SignaturesList() {

    const authRedux = useSelector((state) => state.auth.value);
    const memberRedux = useSelector((state) => state.members.value);

    const role = authRedux.role.id;

    let me = authRedux.me;
    let attachments = memberRedux?.selectedMember?.attachments;

    if (role === 5) {
        attachments = me.attachments;
    }

    const [isEdit, setIsEdit] = React.useState(false);
    const [showDelete, setShowDelete] = React.useState(false);
    const [selected, setSelected] = React.useState([]);

    let list = attachments.filter((data, index) => {
        return data.attachment_type_id === 1;
    })

    const handleSelected = (id) => {

        let arr = selected;
        const found = arr.some(el => el === id);

        if (!found) {
            arr.push(id);
        } else {
            let index = arr.indexOf(id);
            arr.splice(index, 1);
        }

        if (selected.length > 0) {
            setShowDelete(true);
        } else {
            setShowDelete(false);
        }

        setSelected(arr);

    }

    const handleRemove = () => {
        let action = alertServices.delete(selected.length, "Delete selected signatures");
        console.log(action);
    }

    return (
        <div>
            {list.length <= 0 &&
                <Box sx={{ height: 200 }}>

                    <Box sx={{ zIndex: 1 }}>
                        <NoData title="No Signature Attachment Found" />
                    </Box>
                </Box>
            }
            {list.length > 0 &&
                <Paper elevation={0} sx={{ p: 1, m: 1 }}>
                    <Box sx={{ mb: 1 }}>
                        <FormControlLabel sx={{ mr: 1 }} control={<Switch checked={isEdit} onChange={() => setIsEdit(!isEdit)} size="small" sx={{ ml: 1 }} />} label="Edit" />
                        {isEdit &&
                            <React.Fragment>
                                <Button
                                    sx={{
                                        pt: 0,
                                        pb: 0,
                                        width: 75,
                                        '&:hover': {
                                            backgroundColor: 'transparent'
                                        },
                                        '&:before': {
                                            content: "'|'",
                                            ml: -1,
                                            mr: 1,
                                            color: '#000'
                                        },
                                        fontSize: 14,
                                        color: '#1976D2'
                                    }}
                                    type="submit"
                                    fullWidth
                                    variant="text"
                                    color="primary"
                                >
                                    Save
                                    <FaSave size={13} style={{ marginLeft: 5 }} />
                                </Button>
                                {showDelete &&
                                    <Button
                                        sx={{
                                            pt: 0,
                                            pb: 0,
                                            width: 95,
                                            '&:hover': {
                                                backgroundColor: 'transparent'
                                            },
                                            '&:before': {
                                                content: "'|'",
                                                ml: -1,
                                                mr: 1,
                                                color: '#000'
                                            },
                                            fontSize: 14,
                                        }}
                                        type="submit"
                                        fullWidth
                                        variant="text"
                                        color="secondary"
                                        onClick={() => handleRemove()}
                                    >
                                        Remove
                                        <FaTimes size={15} style={{ marginLeft: 5 }} />
                                    </Button>
                                }
                            </React.Fragment>
                        }
                    </Box>
                    <Scrollbars style={{ overflowY: 'hidden', width: '100%', height: 200, backgroundColor: '#eee' }}>
                        <Box
                            display="flex"
                            flexDirection="row"
                            style={{
                                width: '100vw',
                                m: 2,
                                height: 200
                            }}
                        >
                            {
                                list.map((data, index) => (
                                    <React.Fragment key={`imageKey-${index}`}>
                                        {isEdit &&
                                            <Tooltip arrow placement="top" title="Remove?">
                                                <Checkbox
                                                    color="secondary"
                                                    onClick={() => { handleSelected(data.id); }}
                                                    sx={{
                                                        top: 10,
                                                        left: 10,
                                                        position: 'absolute',
                                                    }}
                                                    icon={<BiCircle size={20} />}
                                                    checkedIcon={<FaCheckCircle size={20} />}
                                                />
                                            </Tooltip>
                                        }
                                        <img
                                            width={180}
                                            height={170}
                                            src={`${config.DOMAIN}users/${data.file_path}/${data.file_name}`}
                                            alt={data.file_name}
                                            loading="lazy"
                                            style={{ margin: 10, cursor: 'pointer' }}
                                        />
                                    </React.Fragment>
                                ))
                            }
                        </Box>
                    </Scrollbars>
                </Paper>
            }
        </div>
    )
}
