import React from 'react';
import debounce from 'lodash.debounce';
// Services
import toastService from '../../../services/toast/toastService';
// Redux
import { useDispatch, useSelector } from 'react-redux';
import { me as getMe } from '../../../redux/async/authAsync';
import { getMember } from '../../../redux/async/membersAsync';
// Repository
import addressRepository from '../../../repositories/addressRepository';
// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
import LoadingButton from '@mui/lab/LoadingButton';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import CircularProgress from '@mui/material/CircularProgress';
// Form Components 
import { FaSave } from 'react-icons/fa';

export default function PresentAddressForm(props) {

    const dispatch = useDispatch();
    const authRedux = useSelector((state) => state.auth.value);
    const memberRedux = useSelector((state) => state.members.value);

    const role = authRedux.role.id;

    let me = authRedux?.me;
    let uuid = memberRedux?.selectedMember?.user?.uuid;
    let address = memberRedux?.selectedMember?.address;

    if (role === 5) {
        address = me?.address;
        uuid = me?.user?.uuid;
    }

    const [isEdit, setIsEdit] = React.useState(false);
    const [loading, setLoading] = React.useState(false);

    // Permanent Address
    const [zipcode, setZipcode] = React.useState(address[1]?.zipcode.zipcode ?? "");
    const [street, setStreet] = React.useState(address[1]?.streetpurok ?? "");
    const [brgy, setBrgy] = React.useState(address[1]?.barangay ?? "");
    const [city, setCity] = React.useState(address[1]?.zipcode.area ?? "");
    const [province, setProvince] = React.useState(address[1]?.zipcode.province ?? "");

    const handleChangeZipcode = React.useCallback(async (event) => {
        try {
            if (event.target.value === "") {
                return false;
            }

            setLoading(true);

            let params = {
                zipcode: event.target.value
            }

            let result = await addressRepository.getZipcodeDetails(params);
            if (result.status === 200 && result.data !== "") {
                setCity(result.data.area);
                setProvince(result.data.province);
            }

            setLoading(false);
        } catch (error) {
            toastService.error(error);
        }

    }, []);

    const handleDebounceSearch = React.useMemo(() => {
        return debounce(handleChangeZipcode, 400)
    }, [handleChangeZipcode]);

    const handleSubmit = async (e) => {
        e.preventDefault();

        try {
            setLoading(true);
            setIsEdit(false);

            let data = {
                id: address[0].id,
                uuid: uuid,
                zipcode,
                street,
                brgy,
                city,
                province,
                address_type: 'present'
            }

            await addressRepository.put(data);

            if (role === 5) {
                dispatch(getMe());
            } else {
                dispatch(getMember(data.uuid));
            }

            setIsEdit(true);

        } catch (error) {
            toastService.error(error);
        }
        setLoading(false);
    }


    React.useEffect(() => {
        let mounted = false;

        if (role === 5) {
            dispatch(getMe());
        } else {
            dispatch(getMember(uuid));
        }

        if (!mounted) {

        }

        return () => mounted = true;
    }, [dispatch, uuid, role]);

    return (
        <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
            <div>
                <Paper elevation={0} square sx={{ p: 1, m: 1 }}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sx={{ mb: 1 }}>
                            <FormControlLabel
                                sx={{ mr: 1 }}
                                control={
                                    loading ?
                                        <Typography sx={{ ml: 1 }}>Saving... <CircularProgress size={15} /></Typography>
                                        :
                                        <Switch checked={isEdit} onChange={() => setIsEdit(!isEdit)} size="small" sx={{ ml: 1 }} />}
                                label={loading ? "" : "Edit"}
                                disabled={loading}
                            />
                            {isEdit &&
                                <React.Fragment>
                                    <LoadingButton
                                        sx={{
                                            pt: 0,
                                            pb: 0,
                                            width: 75,
                                            '&:hover': {
                                                backgroundColor: 'transparent'
                                            },
                                            '&:before': {
                                                content: "'|'",
                                                ml: -1,
                                                mr: 1,
                                                color: '#000'
                                            },
                                            fontSize: 14,
                                            color: '#1976D2'
                                        }}
                                        type="submit"
                                        fullWidth
                                        variant="text"
                                        color="primary"
                                        loading={loading}
                                    >
                                        <FaSave size={14} style={{ marginRight: 5 }} />
                                        Save
                                    </LoadingButton>
                                </React.Fragment>
                            }
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setStreet(e.target.value)}
                                value={street}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="street"
                                label="Street/Purok/Block/Zone"
                                name="street"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setBrgy(e.target.value)}
                                value={brgy}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="brgy"
                                label="Barangay"
                                name="brgy"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setZipcode(e.target.value)}
                                onKeyUpCapture={handleDebounceSearch}
                                value={zipcode}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="zipcode"
                                label="Zipcode"
                                name="zipcode"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setCity(e.target.value)}
                                value={city}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="city"
                                label="City / Municipality"
                                name="city"
                                inputProps={
                                    { readOnly: true }
                                }
                                helperText={loading ? "Loading Data..." : "Read-only field"}
                            />
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setProvince(e.target.value)}
                                value={province}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="province"
                                label="Province"
                                name="province"
                                inputProps={
                                    { readOnly: true }
                                }
                                helperText={loading ? "Loading Data..." : "Read-only field"}
                            />
                        </Grid>
                    </Grid>
                </Paper>
            </div>
        </form>
    )
}
