import React, { useRef } from 'react';
import { format, toDate, parseISO } from 'date-fns';
// redux
import { useDispatch, useSelector } from 'react-redux';
import { me as getMe } from '../../../redux/async/authAsync';
import { getMember } from '../../../redux/async/membersAsync';
// repository
import familyRepository from '../../../repositories/familyRepository';
// Services
import toastService from '../../../services/toast/toastService';
// MUI Components
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import LoadingButton from '@mui/lab/LoadingButton';
import Checkbox from '@mui/material/Checkbox';
import FormControlLabel from '@mui/material/FormControlLabel';
// Page Component
import Birthdate from './fields/Birthdate';
import Relations from './fields/Relations';

export default function FamilyMembersForm(props) {

    const dispatch = useDispatch();
    const authRedux = useSelector((state) => state.auth.value);
    const memberRedux = useSelector((state) => state.members.value);

    const role = authRedux.role.id;
    const me = authRedux?.me;
    let uuid = memberRedux?.selectedMember?.user?.uuid;

    const bdate = Object.keys(props.rowData).length > 0 ? parseISO(props?.rowData?.birthdate) : null; 
    
    if (role === 5) {
        uuid = me?.uuid;
    }

    const formRef = useRef();

    const [loading, setLoading] = React.useState(false);

    const [id, setId] = React.useState("");
    const [name, setName] = React.useState("");
    const [birthdate, setBirthdate] = React.useState(bdate);
    const [relations, setRelations] = React.useState("");
    const [selectedRelations, setSelectedRelations] = React.useState("");
    const [isBeneficiary, setIsBeneficiary] = React.useState(false);
    const [isBeneficiaryYes, setIsBeneficiaryYes] = React.useState(false);
    const [isBeneficiaryNo, setIsBeneficiaryNo] = React.useState(false);

    const handleIsRelatedYes = (checked) => {
        setIsBeneficiaryYes(checked);
        if (isBeneficiaryNo && checked) {
            setIsBeneficiaryNo(false);
        }

        if (checked) {
            setIsBeneficiary(true);
        } else {
            setIsBeneficiary(false);
        }
    }

    const handleIsRelatedNo = (checked) => {
        setIsBeneficiary(false);
        setIsBeneficiaryNo(checked);
        if (isBeneficiaryYes && checked) {
            setIsBeneficiaryYes(false);
        }
    }

    const handleSelectedRelations = (relation) => {
        setSelectedRelations(relation);
    }

    const handleStore = async () => {
        try {
            setLoading(true);

            let bdate = format(toDate(birthdate), "Y-M-d");

            let data = {
                uuid,
                name,
                birthdate: bdate,
                relation: relations,
                isBeneficiary,
            }

            await familyRepository.post(data);

            if (role === 5) {
                dispatch(getMe());
            } else {
                dispatch(getMember(data.uuid));
            }

            setLoading(false);

            props.toggle();

        } catch (error) {
            setLoading(false);
            toastService.error(error)
        }
    }

    const handleUpdate = async () => {
        try {
            setLoading(true);

            let bdate = format(toDate(birthdate), "Y-M-d");

            let data = {
                id,
                uuid,
                name,
                birthdate: bdate,
                relation: relations,
                isBeneficiary,
            }

            await familyRepository.put(data);

            if (role === 5) {
                dispatch(getMe());
            } else {
                dispatch(getMember(data.uuid));
            }

            setLoading(false);

            props.toggle();

        } catch (error) {
            setLoading(false);
            toastService.error(error)
        }
    }

    const handleSubmit = (e) => {
        e.preventDefault();

        if (props.action.toLowerCase() === "edit") {
            handleUpdate();
        } else {
            handleStore();
        }

    }

    React.useEffect(() => {
        let mounted = false;

        if (role === 5) {
            dispatch(getMe());
        } else {
            dispatch(getMember(uuid));
        }

        if (!mounted) { 
            if (Object.keys(props.rowData).length > 0) {
                setId(props.rowData.id ?? "");
                setIsBeneficiary(props.rowData.isBeneficiary ?? false);
                if (props.rowData.isBeneficiary) {
                    setIsBeneficiaryYes(true);
                    setIsBeneficiaryNo(false);
                } else {
                    setIsBeneficiaryYes(false);
                    setIsBeneficiaryNo(true);
                }
                setSelectedRelations(props.rowData?.relation?.toLowerCase() ?? "");
                setRelations(props.rowData?.relation?.toLowerCase() ?? "");
                setName(props.rowData.name ?? ""); 
            }
        }

        return () => mounted = true;
    }, [dispatch, uuid, role, props.rowData]);

    return (
        <form ref={formRef} onSubmit={(e) => handleSubmit(e)} autoComplete="off">
            <Grid container spacing={2}>
                <Grid item xs={12}>
                    <Box display="flex" flexDirection="row">
                        <Typography sx={{ mr: 1, p: 1, border: "1px solid #00BFA5", borderRadius: 2, '&:hover': { backgroundColor: "#00BFA5", cursor: 'pointer' } }}>
                            Beneficiary ?
                        </Typography>
                        <FormControlLabel control={<Checkbox sx={{ '&:hover': { backgroundColor: "transparent" } }} checked={isBeneficiaryYes} onChange={(event, checked) => handleIsRelatedYes(checked, "yes")} />} label="Yes" />
                        <FormControlLabel control={<Checkbox sx={{ '&:hover': { backgroundColor: "transparent" } }} checked={isBeneficiaryNo} onChange={(event, checked) => handleIsRelatedNo(checked, "no")} />} label="No" />
                    </Box>
                </Grid>
                <Grid item xs={12}>
                    <Relations setSelectedRelations={(relation) => handleSelectedRelations(relation)} selectedRelations={selectedRelations} relations={relations} setRelations={setRelations} />
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        onChange={(e) => setName(e.target.value)}
                        value={name}
                        variant="outlined"
                        size="small"
                        required
                        fullWidth
                        id="fullname"
                        label="Full Name"
                        name="fullname"
                    />
                </Grid>
                <Grid item xs={12}>
                    <Birthdate setBirthdate={setBirthdate} birthdate={birthdate} variant="outlined" />
                </Grid>
            </Grid>
            <Box display="flex" flexDirection="row" justifyContent="flex-end" sx={{ mt: 2 }}>
                <LoadingButton
                    sx={{ width: 150 }}
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    loading={loading}
                >
                    Create
                </LoadingButton>
                <LoadingButton
                    sx={{ width: 150, ml: 1 }}
                    type="button"
                    fullWidth
                    variant="contained"
                    color="secondary"
                    onClick={() => props.toggle()}
                    loading={loading}
                >
                    Cancel
                </LoadingButton>
            </Box>
        </form>
    )
}
