import React from 'react';

// MUI Components
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';
import countryList from '../constants/countries';


const Country = (props) => {

    const [country, setCountry] = React.useState(props.country ?? '');

    const countryOptions = countryList.map(data => {
        return {
            value: data,
            label: data,
        }
    });

    const handleChange = (value) => {

        setCountry(value);

        if (props.setCountry) {
            props.setCountry(value)
        }

    }

    return (
        <div>
            <TextField
                select
                label="Country"
                name="country"
                size="small"
                variant={props.variant !== null ? props.variant : "outlined"}
                value={country || 'Philippines'}
                onChange={(e) => handleChange(e.target.value)}
                fullWidth
                required={!props.isEdit ?? true}
                inputProps={
                    { readOnly: props.isEdit ?? false }
                }
            >
                {countryOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                        {option.label}
                    </MenuItem>
                ))}
            </TextField>

        </div>
    )
}

export default Country;