import React from 'react'

// Modal Components
import InnerModalDialog from '../shared/Dialog/InnerModalDialog';
import FamilyMembersForm from '../forms/FamilyMembersForm';

export default function FamilyMembersModal(props) {

    return (
        <div>
            <InnerModalDialog open={props.open} title={`${props.action ?? "New "} Family Member`} toggle={props.toggle}>
                <FamilyMembersForm save={props.save} toggle={props.toggle} rowData={props.rowData} action={props.action} />
            </InnerModalDialog>
        </div>
    )
}
