
import React from 'react'
import config from '../../../../config/config';
import { useHistory } from 'react-router-dom';
import { format, parseISO, toDate } from 'date-fns';
// Redux
import { useSelector } from 'react-redux';
// MUI Components
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import Divider from '@mui/material/Divider';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Link from '@mui/material/Link';
// Page Components
import MemberProfileTabs from '../../tabs/MemberProfileTabs';

// react-icons
import {
    FaArrowLeft,
    FaAt,
    FaBirthdayCake,
    FaCalendarDay,
    FaChevronRight,
    FaInfoCircle,
    FaMapMarkerAlt,
    FaPhoneAlt,
    FaUser,
    FaUserEdit
} from 'react-icons/fa';
 

export default function Profile() {

    const history = useHistory();
    const memberRedux = useSelector((state) => state.members.value);

    let profile = memberRedux.selectedMember;
    let address = profile.address;

    let isBirthday = format(parseISO(profile.birthdate), "MMMM dd") === format(toDate(Date.now()), "MMMM dd");
    let birthdate = format(parseISO(profile.birthdate), "MMMM dd, yyyy");

    let avatar_path = `${config.DOMAIN}users/${profile.uuid}/files/avatar/${profile.avatar}`;
    let middle_name = profile.middle_name.length > 0 ? `${profile.middle_name[0]}.` : '';
    let permanentAddress = `${address[0]?.streetpurok}, ${address[0]?.barangay}, ${address[0]?.zipcode.zipcode}`;
    let presentAddress = `${address[1]?.streetpurok}, ${address[1]?.barangay}, ${address[1]?.zipcode.zipcode}`;

    return (
        <div style={{ width: `calc(100vw - 90px)`, height: '100vh', overflow: 'hidden' }}>
            <Paper elevation={0} sx={{ m: 0, p: 0, flexGrow: 1, display: 'flex', height: '100vh' }}>
                <Grid container spacing={1}>
                    <Grid item xs={2} display="flex" flexDirection="column" alignItems="center" sx={{ borderRight: 1, borderColor: 'divider', mt: 0 }}>
                        <Box display="flex" flexDirection="row" justifyContent="center" sx={{ p: 1, ml: 0, width: '100%', backgroundColor: '#eee' }}>
                            <FaUserEdit color='#00BFA5' size={25} style={{ marginRight: 5, marginTop: 2, marginLeft: 5 }} />
                            <Typography variant="h6">
                                Profile
                            </Typography>
                        </Box>
                        <Avatar src={avatar_path} sx={{ width: 200, height: 200, p: 1, mt: 1 }} variant="square" />
                        <Box display="flex" flexDirection="row" justifyContent="flex-start" sx={{ p: 0.2, pl: 2.5, ml: 0, mt: 1, width: '100%', backgroundColor: '#eee' }}></Box>
                        <Button
                            sx={{
                                mt: 1,
                                minWidth: 200,
                                borderRadius: 0,
                                justifyContent: "flex-start",
                                "&:hover": {
                                    ml: 1
                                }
                            }}
                            variant="text"
                            component="label"
                            size="small"
                        >
                            <FaChevronRight size={11} style={{ marginRight: 5 }} />
                            Loans
                        </Button>
                        <Button
                            sx={{
                                mt: 1,
                                minWidth: 200,
                                borderRadius: 0,
                                justifyContent: "flex-start",
                                "&:hover": {
                                    ml: 1
                                }
                            }}
                            variant="text"
                            component="label"
                            size="small"
                            onClick={() => history.push('/deposits')}
                        >
                            <FaChevronRight size={11} style={{ marginRight: 5 }} />
                            Deposit
                        </Button>
                        <Button
                            sx={{
                                mt: 1,
                                minWidth: 200,
                                borderRadius: 0,
                                justifyContent: "flex-start",
                                "&:hover": {
                                    ml: 1
                                }
                            }}
                            variant="text"
                            component="label"
                            size="small"
                            onClick={() => history.push('/withdrawals')}
                        >
                            <FaChevronRight size={11} style={{ marginRight: 5 }} />
                            Withdraw
                        </Button>
                        <Button
                            sx={{
                                mt: 1,
                                minWidth: 200,
                                borderRadius: 0,
                                justifyContent: "flex-start",
                                "&:hover": {
                                    ml: 1
                                }
                            }}
                            variant="text"
                            component="label"
                            size="small"
                        >
                            <FaChevronRight size={11} style={{ marginRight: 5 }} />
                            Payments
                        </Button>
                        <Button
                            sx={{
                                mt: 1,
                                minWidth: 200,
                                borderRadius: 0,
                                justifyContent: "flex-start",
                                "&:hover": {
                                    ml: 1
                                }
                            }}
                            variant="text"
                            component="label"
                            size="small"
                        >
                            <FaChevronRight size={11} style={{ marginRight: 5 }} />
                            Send Message
                        </Button>
                    </Grid>
                    <Grid item xs={10}>
                        <Box sx={{ p: 0, mt: 1, pl: 1 }}>
                            <Link onClick={() => history.goBack()} sx={{ cursor: 'pointer' }}>
                                <FaArrowLeft size={12} style={{ marginRight: 5 }} />
                                Back
                            </Link>
                        </Box>
                        <Paper elevaiont={2} square sx={{ backgroundColor: '#f1f2f6' }}>
                            <Box sx={{ m: 0, mt: 1 }}>
                                <Box
                                    display="flex"
                                    flexGrow={1}
                                    flexDirection="column"
                                    sx={{
                                        mb: 1,
                                        width: '100%',
                                        backgroundColor: "#00BFA5",
                                        color: "#FFF"
                                    }}>
                                    <Typography variant="h6" sx={{ p: 1, pl: 2, mt: 1 }}>
                                        <FaInfoCircle size={18} style={{ marginRight: 5, color: '#fff' }} />
                                        Quick Overview
                                    </Typography>
                                    <Box display="flex" flexDirection="row" justifyContent="flex-start" sx={{ width: "100%", mt: -2 }}>
                                        <Box sx={{ width: "31.5%", m: 1 }}>
                                            <Paper elevation={2} sx={{ p: 1, pl: 2, m: 1, width: '100%', display: "flex", flexDirection: "row" }}>
                                                <Box sx={{ width: '75%', borderRight: '1px solid #eee' }}>
                                                    <Typography variant="subtitle1">Full Name</Typography>
                                                    <Typography variant="h6" noWrap>{`${profile.first_name} ${middle_name} ${profile.last_name}`}</Typography>
                                                    {isBirthday &&
                                                        <Typography variant="caption" color="secondary" noWrap>
                                                            <FaBirthdayCake /> Today is {profile?.gender.toLowerCase() === "male" || profile?.gender.toLowerCase() === "m" ? 'his' : 'her'} birthday
                                                        </Typography>
                                                    }
                                                    {!isBirthday &&
                                                        <Typography variant="caption">
                                                            <FaCalendarDay /> Date of Birth: {birthdate ?? '...'}
                                                        </Typography>
                                                    }
                                                </Box>
                                                <Box sx={{ width: '25%' }} display="flex" flexDirection="row" justifyContent="center" alignItems="center">
                                                    <FaUser size={50} style={{ color: "#00BFA5", opacity: 0.7 }} />
                                                </Box>
                                            </Paper>
                                        </Box>
                                        <Box sx={{ width: "31.5%", m: 1 }}>
                                            <Paper elevation={2} sx={{ p: 1, pl: 2, m: 1, width: '100%', display: "flex", flexDirection: "row" }}>
                                                <Box sx={{ width: '75%', borderRight: '1px solid #eee' }}>
                                                    <Typography variant="subtitle1">Contact Info</Typography>
                                                    <Typography variant="h6" noWrap>{profile.contact_no ?? '...'}</Typography>
                                                    <Typography variant="caption" noWrap>
                                                        <FaAt /> Email: {profile.user.email ?? '...'}
                                                    </Typography>
                                                </Box>
                                                <Box sx={{ width: '25%' }} display="flex" flexDirection="row" justifyContent="center" alignItems="center">
                                                    <FaPhoneAlt size={50} style={{ color: "#1976D2", opacity: 0.7 }} />
                                                </Box>
                                            </Paper>
                                        </Box>
                                        <Box sx={{ width: "31.5%", m: 1 }}>
                                            <Paper elevation={2} sx={{ p: 1, pl: 2, m: 1, width: '100%', display: "flex", flexDirection: "row" }}>
                                                <Box sx={{ width: '75%', borderRight: '1px solid #eee' }}>
                                                    <Typography variant="subtitle1">Present Address</Typography>
                                                    <Typography variant="h6" noWrap> {presentAddress ?? '...'}</Typography>
                                                    <Typography variant="caption" noWrap>
                                                        <FaMapMarkerAlt /> Permanent: {permanentAddress ?? '...'}
                                                    </Typography>
                                                </Box>
                                                <Box sx={{ width: '25%' }} display="flex" flexDirection="row" justifyContent="center" alignItems="center">
                                                    <FaMapMarkerAlt size={50} style={{ opacity: 0.7, color: '#E45570' }} />
                                                </Box>
                                            </Paper>
                                        </Box>
                                    </Box>
                                </Box>
                            </Box>
                        </Paper>
                        <Divider sx={{ mt: 2 }} />
                        <MemberProfileTabs />
                    </Grid>
                </Grid>
            </Paper>
        </div>
    )
}
