import React from 'react'
// Modal Components
import LargeModalDialog from '../shared/Dialog/LargeModalDialog';  
import NewEmploymentDetailsForm from '../forms/NewEmploymentDetailsForm';

export default function EmploymentModal(props) {
    return (
        <div>
            <LargeModalDialog open={props.open} title={`New Employment Details`} toggle={props.toggle} size="md">
                  <NewEmploymentDetailsForm toggle={props.toggle} />
            </LargeModalDialog>
        </div>
    )
}
