import * as React from 'react';

// Redux
import { useSelector } from 'react-redux';

// MUI Components
import Box from '@mui/material/Box';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';

// Page Components
import TabPanel from './components/TabPanel1';
import IDPresentedList from '../lists/IDPresentedList';
import SignaturesList from '../lists/SignaturesList';
import AddressSketchList from '../lists/AddressSketchList';

// react-icons
import { FaFolder } from 'react-icons/fa';

export default function AttachmentTabs() {

    const attachmentTypesRedux = useSelector((state) => state.masterfiles.value);
    let attachmentTypes = attachmentTypesRedux.masterfiles.attachmentTypes;

    const [value, setValue] = React.useState(0);

    const handleChange = (newValue) => {
        setValue(newValue);
    };

    return (
        <Box sx={{ m: 1, bgcolor: 'background.paper' }}>
            <Paper sx={{ p: 2 }}>
                <Tabs
                    value={value}
                    sx={{ p: 1 }}
                >
                    {
                        attachmentTypes.map((data, index) => {
                            if(index === 3){
                                return false;
                            }
                            return <Tab key={`tabkey-${index}`} onClick={() => handleChange(index)} label={
                                <Typography variant="caption1">
                                    <Box display="flex">
                                        <FaFolder size={15} style={{ marginRight: 5 }} />
                                        {data.type}
                                    </Box>
                                </Typography>
                            } />
                        })
                    }
                </Tabs>
                <TabPanel value={value} index={0}>
                    <SignaturesList />
                </TabPanel>
                <TabPanel value={value} index={1}>
                    <IDPresentedList />
                </TabPanel>
                <TabPanel value={value} index={2}>
                    <AddressSketchList />
                </TabPanel>
            </Paper>
        </Box>
    );
}
