import React from 'react';
import utilityService from '../../../../services/utils/utilityServices';

// Redux
import { useSelector } from 'react-redux';

// MUI Components
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';

// Page Component
import LinearProgressBar from '../../shared/LinearProgressBar/LinearProgressBar';



const BusinessTypes = (props) => {

    const businessTypeRedux = useSelector((state) => state.masterfiles.value);
    const [businessType, setBusinessType] = React.useState(props.businessType ?? '');

    const businessTypeOptions = businessTypeRedux.masterfiles.businessTypes.map(data => {
        return {
            value: data.id,
            label: utilityService.capitalizeFirstLetter(data.type)
        }
    });

    const handleChange = (value) => {

        setBusinessType(value)
        if (props.setBusinessType) {
            props.setBusinessType(value)
        }

        if (props.setLabel) {
            let label = businessTypeRedux.businessTypes.filter(data => {
                return value === data.id;
            });

            props.setLabel(label[0].type)
        }
    }


    return (
        <div>
            {businessTypeRedux.loading && <LinearProgressBar title={businessTypeRedux.status} />}
            {!businessTypeRedux.loading &&
                <TextField
                    select
                    label="Business Type"
                    name="businessType"
                    size="small"
                    variant={props.variant !== null ? props.variant : "outlined"}
                    value={businessType}
                    onChange={(e) => handleChange(e.target.value)}
                    fullWidth
                    required={!props.isEdit ?? true}
                    disabled={businessTypeRedux.loading}
                    inputProps={
                        { readOnly: props.isEdit ?? false }
                    }
                >
                    {businessTypeOptions.map((option) => (
                        <MenuItem key={option.value} value={option.value}>
                            {option.label}
                        </MenuItem>
                    ))}
                </TextField>
            }
        </div>
    )
}

export default BusinessTypes;