import React, { useMemo } from 'react';

// MUI Components
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';

const Relations = (props) => {

    const [relations, setRelations] = React.useState(props.selectedRelations ?? '');
    const [others, setOthers] = React.useState("");

    const relationsOptions = useMemo(() => {
        return [
            {
                value: "grand father",
                label: "Grand Father"
            },
            {
                value: "grand mother",
                label: "Grand Mother"
            },
            {
                value: "father",
                label: "Father"
            },
            {
                value: "mother",
                label: "Mother"
            },
            {
                value: "brother",
                label: "Brother"
            },
            {
                value: "sister",
                label: "Sister"
            },
            {
                value: "daugther",
                label: "Daugther"
            },
            {
                value: "son",
                label: "Son"
            },
            {
                value: "niece",
                label: "Niece"
            },
            {
                value: "nephew",
                label: "Nephew"
            },
            {
                value: "auntie",
                label: "Auntie"
            },
            {
                value: "uncle",
                label: "Uncle"
            },
            {
                value: "others",
                label: "Others"
            },
        ];
    }, []);

    const handleChange = (value) => {

        setRelations(value);
        if (props.setRelations && props.setSelectedRelations) {
            if (value === 'others') {
                props.setSelectedRelations("others");
            } else {
                setOthers("");
                props.setSelectedRelations(value);
                props.setRelations(value);
            }
        }

    }

    const handleChangeothers = (value) => {
        setOthers(value);
        if (props.setRelations) {
            props.setRelations(value.toLowerCase());
        }
    }

    React.useEffect(() => {
        let mounted = false;

        if (!mounted) {
            let filtered = relationsOptions.filter(data => data.value === props.selectedRelations.toLowerCase());
            if (filtered === 0) {
                setRelations("others");
            } else {
                setRelations(props.selectedRelations.toLowerCase());
            }
        }

        return () => mounted = true;
    }, [relationsOptions, props.selectedRelations])

    return (
        <div>
            <TextField
                select
                label="Relation"
                name="relation"
                size="small"
                variant="outlined"
                value={relations}
                onChange={(e) => handleChange(e.target.value)}
                fullWidth
                required
            >
                {relationsOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                        {option.label}
                    </MenuItem>
                ))}
            </TextField>
            {relations === "others" &&
                <TextField
                    label="Specify Others"
                    placeholder='If others please specify'
                    name="relation"
                    size="small"
                    variant="outlined"
                    value={others}
                    onChange={(e) => handleChangeothers(e.target.value)}
                    fullWidth
                    required
                    sx={{ mt: 2 }}
                />
            }
        </div>
    )
}

export default Relations;